.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
  display:inline-block;
  width: 10vmin;
  padding: 10px;
  padding-bottom: 0px;
}

.App-header {
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: left;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
  border-style:solid;
  border-bottom-width: 1px;
  border-width: 0px 0px 1px 0px;
  border-color:black;
  position:sticky;
    top:0;
  backdrop-filter:blur(10px);
    box-shadow: 2px 7px 7px 1px rgba(0,0,0,0.3);
}

.header-button{
  text-decoration: none;
  color: black;
  margin: 10px;
  margin-top:30px;
  margin-bottom: 0px;
}

.log{
  align-items:right;
  font-size: 15px;
  margin-top:38px;
  margin-left: 1050px;
}

.sign{
  margin-left: 10px;
}